let TimeIntervalForRender = -1;
let szHandle = [];
let TIME_PER_FRAME = 1000 / 25;
let TIME_SLICE = 1000 / 50;
let HVC_Decode_Render_Ctrl = {
    TIME_SLICE: TIME_PER_FRAME,

    beginHandle: function (HVCCtrl) {
        if (szHandle.indexOf(HVCCtrl) < 0) {
            szHandle.push(HVCCtrl);
        }

        if (szHandle.length == 1) {
            this._beginTimer();
        }
    },
    endHandle: function (HVCCtrl) {
        if (szHandle.indexOf(HVCCtrl) >= 0) {
            szHandle.splice(szHandle.indexOf(HVCCtrl), 1);
        }

        if (szHandle.length == 0) {
            this._endTimer();
        }
    },

    _timeCallback: function () {
        for (let ii = 0; ii < szHandle.length; ii++) {
            szHandle[ii].HandleRender();
        }
    },

    _beginTimer: function () {
        if (TimeIntervalForRender == -1) {
            TimeIntervalForRender = setInterval(this._timeCallback, TIME_SLICE);
        }
    },

    _endTimer: function () {
        if (TimeIntervalForRender != -1) {
            clearInterval(TimeIntervalForRender);
            TimeIntervalForRender = -1;
        }
    }
};

export default HVC_Decode_Render_Ctrl;