import Log from '../utils/logger.js';

//import HVCDecode from '../../lib/dist/libffmpeg_265.js';
import DecodeCtrl from './hevc_decode_render_ctrl.js';

let DECODER_H265 = 1;
let LOG_LEVEL_WASM = 1;
let HVCDecode = null;
let INVALID_DECODER = -10;
let DecoderMap = [];

class HVCController {
    constructor() { //this.decoder = new FFmpegDecode();

        if (HVCDecode == null) {
            if (typeof window.Module != 'undefined') {
                HVCDecode = window.Module;
            }
        }
        this.decoder = INVALID_DECODER;
        this.lastBuffer = 0;
        this.lastBufferSize = -1;
        this.webglPlayer = null;
        this._mediaElement = null;
        this._canvasElement = null;
        this._szDecoderArray = [];
        this._szRendArray = [];
        this._beginTimeStamp = 0;
        this._beginDecodeStamp = 0;
        this._enterHidden = false;
    }

    _getAPacketToDecode() {
        if (this._szDecoderArray != null && this._szDecoderArray.length > 0) {
            return this._szDecoderArray.shift();
        }
        return null;
    }

    _pushAPacketToDecode(packet) {
        if (this._szDecoderArray != null) {

            if (this._beginDecodeStamp == 0) {
                if (packet.key == false) {
                    return;
                }

                this._beginDecodeStamp = new Date().getTime();
            }
            this._szDecoderArray.push(packet);
        }
    }

    _getAFrameToRender() {
        if (this._szRendArray != null) {
            if (this._beginTimeStamp == 0) {
                this._beginTimeStamp = new Date().getTime();
            }
            return this._szRendArray.shift();
        }
        return null;
    }

    _pushAFrameToRender(frame) {
        if (this._szRendArray != null) {
            this._szRendArray.push(frame);
            //console.log('Push Frame To Array:' + this._szRendArray.length);
        }
        return null;
    }

    _renderAFrame() {
        let data = this._getAFrameToRender();
        //console.log('Render Frame From Array:' + this._szRendArray.length);
        if (data != null) {
            this.displayVideoFrame(data);
        }
    }

    _decodeAPacket() {
        let packet = this._getAPacketToDecode();
        if (packet != null) {
            this._decodeData(packet.data, packet.pts);
        }
    }

    attachMediaElement(mediaElement) {
        this._mediaElement = mediaElement;
        this._canvasElement = document.createElement('canvas');
        this._mediaElement.parentElement.appendChild(this._canvasElement);
        this._mediaElement.hidden = true;
        this._canvasElement.style.height = '100%';
        this._canvasElement.style.width = '100%';
        this.webglPlayer = new window.WebGLPlayer(this._canvasElement, {
            preserveDrawingBuffer: false
        });

        this._mediaElement.style.display = 'none';
    }

    _onVideoDecodeCallback(handle, addr_y, addr_u, addr_v, stride_y, stride_u, stride_v, width, height, pts) {
        let size = width * height + (width / 2) * (height / 2) + (width / 2) * (height / 2);
        let data = new Uint8Array(size);
        let pos = 0;
        for (let i = 0; i < height; i++) {
            let src = addr_y + i * stride_y;
            let tmp = HVCDecode.HEAPU8.subarray(src, src + width);
            tmp = new Uint8Array(tmp);
            data.set(tmp, pos);
            pos += tmp.length;
        }
        for (let i = 0; i < height / 2; i++) {
            let src = addr_u + i * stride_u;
            let tmp = HVCDecode.HEAPU8.subarray(src, src + width / 2);
            tmp = new Uint8Array(tmp);
            data.set(tmp, pos);
            pos += tmp.length;
        }
        for (let i = 0; i < height / 2; i++) {
            let src = addr_v + i * stride_v;
            let tmp = HVCDecode.HEAPU8.subarray(src, src + width / 2);
            tmp = new Uint8Array(tmp);
            data.set(tmp, pos);
            pos += tmp.length;
        }
        let obj = {
            data: data,
            width,
            height,
            handle: handle,
            pts: pts,
        };

        if (DecoderMap[handle]) {
            DecoderMap[handle]._pushAFrameToRender(obj);
        }
    }

    displayVideoFrame(obj) {
        let data = new Uint8Array(obj.data);
        let width = obj.width;
        let height = obj.height;
        let yLength = width * height;

        // let parentHeight = this._mediaElement.parentElement.offsetHeight;
        // let parentWidth = this._mediaElement.parentElement.offsetWidth;
        let desHeight = 0;
        let desWidth = 0;

        // if (parentHeight < height) {
        //     desHeight = parentHeight;
        // } else {
        //     desHeight = height;
        // }
        desHeight = height;

        if (this._canvasElement.height != desHeight) {
            this._canvasElement.height = desHeight;
        }


        // if (parentWidth < width) {
        //     desWidth = parentWidth;
        // } else {
        //     desWidth = width;
        // }

        desWidth = width;
        if (this._canvasElement.width != desWidth) {
            this._canvasElement.width = desWidth;
        }

        let uvLength = (width / 2) * (height / 2);
        this.webglPlayer.renderFrame(data, width, height, yLength, uvLength);
    }


    appendInitSegment(initSegment) {
        if (this.decoder == INVALID_DECODER) {
            let videoCallback = HVCDecode.addFunction(this._onVideoDecodeCallback, 'viiiiiiiiii');
            let handle = HVCDecode._openDecoder(DECODER_H265, videoCallback, LOG_LEVEL_WASM);
            if (handle >= 0) {
                this.decoder = handle;
                DecoderMap[handle] = this;
                this._decodeData(initSegment.data, 0);
                DecodeCtrl.beginHandle(this);
            }
        }
    }

    appendMediaSegment(mediaSegment) {

        if (this.decoder != INVALID_DECODER) {
            let sample = mediaSegment.data.shift();

            while (sample) {

                if (document.visibilityState == 'visible') {

                    if (this._enterHidden == true) {
                        if (sample.isKeyframe == false) {
                            sample = mediaSegment.data.shift();
                            continue;
                        } else {
                            this._enterHidden = false;
                        }
                    }


                    this._pushAPacketToDecode({
                        data: sample.units[0].data,
                        pts: sample.pts,
                        key: sample.isKeyframe
                    });
                } else {
                    this._enterHidden = true;
                }

                //console.log('xxxxxxxxxxxxxx:' + sample.pts + 'xxx:' + sample.units.length);
                sample = mediaSegment.data.shift();
            }
        }
    }

    _decodeData(data, pts) {

        if (document.visibilityState == 'hidden') {
            return;
        }

        let size = data.length;
        //console.log('decode data length' + size);
        let buffer = HVCDecode._malloc(size);
        HVCDecode.HEAPU8.set(data, buffer);
        HVCDecode._decodeData(buffer, size, pts, this.decoder);
        HVCDecode._free(buffer);
    }

    destroy() {
        if (this.decoder != INVALID_DECODER) {
            DecodeCtrl.endHandle(this);
            this._szRendArray = null;
            this._szDecoderArray = null;
            HVCDecode._flushDecoder(this.decoder);
            HVCDecode._closeDecoder(this.decoder);
            delete DecoderMap[this.decoder];
            this.decoder = INVALID_DECODER;
            this._mediaElement.parentElement.removeChild(this._canvasElement);
            this._mediaElement.hidden = false;
            this._mediaElement.style.display = 'block';
        }
    }

    HandleRender() {

        //console.log('time tick comes decode array length:' + this._szDecoderArray.length + 'render array length:' + this._szRendArray.length);

        let currentPts = new Date().getTime() - this._beginTimeStamp;

        this._decodeAPacket();

        if (this._szRendArray.length > 0) {
            let intervalCurrent = currentPts - this._szRendArray[0].pts;

            if (intervalCurrent > DecodeCtrl.TIME_SLICE) {
                this._decodeAPacket();
            }

            if (intervalCurrent > 0) {
                this._renderAFrame();
            }

        }
    }
}

export default HVCController;